.mainWrapper {
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    height: calc(100vh - 107px);
    justify-content: space-between;
    align-items: center;

}

.content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    max-width: 480px;


}

.title {
    font-weight: 700;
    font-size: 48px;
    line-height: 115.8%;
    margin-bottom: 32px;
}

.title span {
    color: #357EFE;
}

.text {
    font-weight: 400;
    font-size: 18px;
    color: #232727;
    line-height: 130%;
    max-width: 285px;
    margin-bottom: 42px;
}

.btnGroup {
    display: flex;
    align-items: center;
    gap: 8px;
}


.contentVideo {
    width: 692px;
    height: 469px;
    overflow: hidden;

}

.video {
    width: 781px;
    height: 529px;
    object-fit: cover;
}



@media (max-width: 1250px) {
    .mainWrapper {
        padding: 24px 18px 80px 18px;
        height: auto;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .contentVideo {
        order: 1;
    }

    .content {
        order: 2;
        align-items: center;
        max-width: 692px;
    }

    .title {
        margin-bottom: 24px;
    }

    .text {
        max-width: 692px;
        text-align: center;
        margin-bottom: 32px;
    }

    .btnGroup {
        gap: 60px
    }

}

@media (max-width: 757px) {
    .contentVideo {
        width: 500px;
        height: 300px;
    }

    .video {
        width: 500px;
        height: 300px;
    }

    .content {
        max-width: 500px;
    }

    .title {
        text-align: center;
    }

    .btnGroup {
        gap: 10px
    }

}

@media (max-width: 565px) {
    .contentVideo {
        width: 341px;
        height: 215px;
    }

    .video {
        width: 341px;
        height: 215px;
    }

    .content {
        max-width: 343px;
    }

    .title {
        font-weight: 700;
        font-size: 32px;
        line-height: 40px;
    }

    .btnGroup {
        flex-direction: column;
        align-items: center;
        gap: 16px;
    }

    .btnGroup:first-child {
        margin-bottom: 16px;
    }



}
